

.title {
  text-align: start;
  font-size: 50px;
  font-weight: bold;
  margin: 0;
  padding: 20px;
  color: var(--primary-color);
  font-family: 'Josefin Sans', sans-serif;
  
}

@media (max-width: 900px) {
  .title {
    font-size: 30px;
    text-align: center;
  }
}

.description {
  font-size: 20px;
  line-height: 1.5;
  margin-left: 20px;
  text-align: start;
  max-width: 900px;
}

@media (max-width: 1000px) {
  .description {
    font-size: 25px;
    text-align: center;
    margin-top: 40px;
  }
}

.container {
  padding-top: 150px;
  /*margin-left: 10px;*/
  max-width: 90%;
  margin-left: 40px;
}

@media (max-width: 1000px) {
  .container {
    padding-top: 0;
    margin-top: 100px;
    text-align: center;
    align-items: center;
    justify-content: center;
  }
}

.rounded-shadow-button {
  font-family: 'Josefin Sans', sans-serif;
  padding: 10px 20px;
  border-radius: 30px;
  font-size: 18px;
  background-color: #20387A;
  color: #fff;
  border: none;
  box-shadow: 0 0 10px #ccc;
  cursor: pointer;
  width: 170px;
  height: 50px;
  margin-top: 80px;
}

@media (max-width: 1000px) {
  .rounded-shadow-button {
    font-size: 30px;
    width: 350px;
    border-radius: 50px;
    height: 100px;
    margin-top: 150px;
  }
}

#scroll-icon {
  width: 100%;
  height: 70vh;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  z-index: 1;
}

#scroll-icon i {
  font-size: 20px;
  color: #fff;
  background-color: transparent;
  border: 2px solid #fff;
  border-radius: 50%;
  padding: 10px;
}

@media only screen and (max-width: 1000px) {
  #scroll-icon {
    display: none;
  }
}